@font-face {
  font-family: "Poppins";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/Poppins-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/Poppins-Medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-weight: 600;
  font-style: normal;
  src: url("../fonts/Poppins-SemiBold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/Poppins-Bold.woff2") format("woff2");
  font-display: swap;
}
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.page {
  background-color: hsl(158, 41%, 95%);
  font-family: "Poppins";
  padding-top: 1.875em;
  padding-bottom: 1.875em;
  overflow-x: hidden;
}
@media (min-width: 1921px) {
  .page {
    font-size: 0.8333333vw;
  }
}
@media (max-width: 1366px) {
  .page {
    font-size: 14px;
  }
}

@media (min-width: 1000px) {
  .mobile-only {
    display: none !important;
  }
}

@media (max-width: 1000px) {
  .desktop-only {
    display: none !important;
  }
}

button {
  cursor: pointer;
  font-family: "Poppins";
}

.container {
  max-width: 69.375em;
  margin: 0 auto;
  padding: 0em 0.9375em;
}

.header {
  margin-bottom: 7.5em;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-content__button {
  font-size: 1em;
  background-color: hsl(144, 44%, 50%);
  border: none;
  font-weight: 500;
  border-radius: 1.25em;
  padding: 0.5em 1.5em;
  color: hsl(0, 0%, 100%);
}
@media (max-width: 1000px) {
  .header-content__button {
    margin-top: 2.5em;
    margin-left: 0.5em;
  }
}
.header-content__info {
  display: flex;
  gap: 14.0625em;
  align-items: center;
  transition: transform 0.3s;
  z-index: 10;
}
@media screen and (max-width: 1000px) {
  .header-content__info {
    align-items: flex-start;
    flex-direction: column;
    padding: 1.25em;
    height: 100vh;
    width: 17.5em;
    padding-top: 3.125em;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
    background: hsl(0, 0%, 100%);
    gap: 0.625em;
  }
}
@media screen and (max-width: 1000px) {
  .header-content__info--open {
    transform: translateX(0);
    box-shadow: 0.625em 0.625em 2.8125em hsl(0, 0%, 0%);
  }
}
.header-content__close {
  background: none;
  border: none;
  width: 1.875em;
  height: 1.875em;
  position: absolute;
  top: 0.9375em;
  right: 0.9375em;
}
.header-content__close::before, .header-content__close::after {
  content: "";
  position: absolute;
  width: 100%;
  display: block;
  height: 1px;
  background-color: #2d2d2d;
}
.header-content__close::before {
  transform: rotate(-45deg);
}
.header-content__close::after {
  transform: rotate(45deg);
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.logo__img {
  width: 2.5em;
  height: 2.5em;
  margin-right: 0.5em;
}
.logo__text {
  font-size: 1.5625em;
  color: hsl(0, 0%, 18%);
  font-weight: 500;
}

.menu {
  display: flex;
  gap: 3.125em;
}
@media screen and (max-width: 1000px) {
  .menu {
    flex-direction: column;
    gap: 1.875em;
  }
}
.menu__link {
  position: relative;
  color: hsl(0, 0%, 29%);
  text-decoration: none;
}
@media screen and (max-width: 1000px) {
  .menu__link {
    padding-left: 1.875em;
  }
}
.menu__link:hover::after, .menu__link--active::after {
  content: "";
  width: 1.3125em;
  height: 2px;
  position: absolute;
  bottom: -0.625em;
  left: 50%;
  transform: translateX(-50%);
  background-color: hsl(21, 95%, 70%);
}
@media screen and (max-width: 1000px) {
  .menu__link:hover::after, .menu__link--active::after {
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}

.orange {
  color: hsl(20, 100%, 64%);
}

.main-content {
  display: flex;
}
@media (max-width: 1000px) {
  .main-content {
    flex-direction: column-reverse;
  }
}
.main-content__column {
  width: 50%;
}
@media (max-width: 1000px) {
  .main-content__column {
    width: 100%;
  }
}
.main-content__title {
  font-size: 3em;
  display: block;
  color: hsl(0, 0%, 18%);
  font-weight: 700;
  margin-bottom: 0.2083333333em;
  line-height: 1.3541666667em;
}
@media (max-width: 1000px) {
  .main-content__title {
    text-align: center;
  }
}
.main-content__subtitle {
  font-size: 0.9375em;
  display: block;
  color: hsl(0, 0%, 49%);
  line-height: 1.7333333333em;
  font-weight: 500;
  margin-bottom: 2em;
  max-width: 29.6em;
}
@media (max-width: 1000px) {
  .main-content__subtitle {
    max-width: 100%;
  }
}
.main-content__button {
  font-size: 0.9375em;
  font-weight: 500;
  background-color: hsl(144, 44%, 50%);
  border: none;
  border-radius: 2em;
  padding: 0.6666666667em 1.6em;
  color: hsl(0, 0%, 100%);
  margin-bottom: 3.3333333333em;
}

.main-content-search {
  display: flex;
  align-items: center;
  position: relative;
  max-width: 37.3125em;
  height: 3.875em;
  margin-bottom: 2.5em;
}
.main-content-search__input {
  font-size: 1em;
  border: none;
  width: 100%;
  height: 3.875em;
  background-color: hsl(0, 0%, 100%);
  border-radius: 6.25em;
  padding-left: 2.5em;
  padding-right: 4.375em;
}
.main-content-search__img {
  width: 1.875em;
}
.main-content-search__button {
  font-size: 1em;
  position: absolute;
  right: 0.625em;
  width: 3.4375em;
  height: 3.4375em;
  border-radius: 50%;
  border: none;
  color: hsl(0, 0%, 100%);
  background-color: hsl(144, 44%, 50%);
}

.main-content-info {
  display: flex;
  align-items: center;
}
.main-content-info__users {
  display: flex;
  margin-right: 0.75em;
}
.main-content-info__text {
  color: hsl(0, 0%, 44%);
  font-weight: 400;
}
.main-content-info__link {
  color: hsl(20, 98%, 67%);
  font-weight: 500;
  text-decoration: none;
}
.main-content-info__img {
  position: relative;
  width: 2.375em;
  height: 2.375em;
  z-index: 2;
}
.main-content-info__img:nth-child(1) {
  z-index: 4;
}
.main-content-info__img:nth-child(2) {
  z-index: 3;
}
.main-content-info__img:nth-child(3) {
  z-index: 2;
}
.main-content-info__img:not(:first-child) {
  margin-left: -1em;
}
.main-content-info__button {
  font-size: 1.25em;
  position: relative;
  z-index: 1;
  margin-left: -0.8em;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.6em;
  height: 1.6em;
  color: hsl(0, 0%, 100%);
  border-radius: 50%;
  border: 3px solid hsl(0, 0%, 100%);
  background-color: hsl(20, 100%, 64%);
}

.header-gamburger {
  background: none;
  border: none;
  position: relative;
  width: 1.6875em;
  height: 1.6875em;
  cursor: pointer;
  display: inline-flex;
  margin-left: auto;
}
.header-gamburger::after, .header-gamburger::before, .header-gamburger__line {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  display: block;
  background-color: hsl(0, 0%, 18%);
}
.header-gamburger::before {
  right: 0;
  bottom: 0;
  margin-bottom: 0;
}
.header-gamburger::after {
  top: 1px;
  left: 0;
  margin-top: 0;
}
.header-gamburger__line {
  top: 50%;
  left: 0;
  margin-top: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.illustration {
  position: relative;
  display: flex;
  justify-content: center;
}
@media (max-width: 1000px) {
  .illustration {
    font-size: 12px;
  }
}
@media (max-width: 480px) {
  .illustration {
    font-size: 8px;
  }
}
.illustration__women {
  position: relative;
  width: 16.9375em;
  height: 34.8125em;
  background-image: url("../img/women.png");
  background-size: cover;
  z-index: 3;
}
.illustration__ellipse {
  position: absolute;
  top: 5em;
  z-index: 2;
  width: 17.5em;
  height: 17.5em;
  border-radius: 50%;
  background-color: hsl(144, 44%, 50%);
}
.illustration__thin {
  position: absolute;
  top: -2.5em;
  border-radius: 50%;
  width: 28.125em;
  height: 28.125em;
  border: 2px #E8E8E8 solid;
  z-index: 1;
}
.illustration__thin--animation {
  animation: rotation 10s infinite ease-in-out;
}

.flag {
  border: 0.625em hsl(0, 0%, 100%) solid;
  position: absolute;
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  left: 50%;
  top: 50%;
  transition: 0.3s ease-in;
}
.flag img {
  width: 100%;
}
.flag:hover {
  transform: scale(1.5);
}
.flag--gb {
  width: 3.75em;
  height: 3.75em;
  left: -0.625em;
  top: 4.4375em;
}
.flag--italia {
  width: 2.5em;
  height: 2.5em;
  left: 13.5em;
  top: -2.1875em;
}
.flag--india {
  width: 3.125em;
  height: 3.125em;
  left: 24.25em;
  top: 5.1875em;
}
.flag--india-2 {
  width: 1.875em;
  height: 1.875em;
  left: -1.125em;
  top: 15.1875em;
}
.flag--india-3 {
  width: 1.875em;
  height: 1.875em;
  left: 26.25em;
  top: 15.0625em;
}